<template>
  <li
    class="beauti-contextmenu-submenu"
    ref="submenu"
    @mouseenter="showSubmenuHandler"
    @mouseleave="hideSubmenuHandler"
  >
    <span v-if="icon" class="icon"></span>
    <span class="content">{{ label }}</span>
    <span class="more"><img width="16" height="16" src="data:image/svg+xml;base64,"/></span>

    <ul class="beauti-contextmenu-sub-ul" ref="subMenuListRef" v-show="showSubmenu" :style="menuPos">
      <slot></slot>
    </ul>
  </li>
</template>
<script>
import { _fixesContextPos } from '../../Utils'
export default {
  name: 'BeautiContextSubmenu',
  inject: ['$$BeautiContextmenu'],
  props: {
    icon: {
      type: String
    },
    label: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      enterTimer: null,
      outTimer: null,
      menuPos: {},
      showSubmenu: false
    }
  },
  watch: {
    showSubmenu (isShow) {
      if (!isShow) {
       this.menuPos = {}
       this.$$BeautiContextmenu.zIndex--
      }
    }
  },
  methods: {
    initMenuPos () {
      const subMenuRef = this.$refs.submenu
      const subMenuListRef = this.$refs.subMenuListRef
      const zIndex = this.$$BeautiContextmenu.zIndex
      this.$$BeautiContextmenu.zIndex++
      if (subMenuRef) {
        this.$nextTick(() => {
          const menuPos = _fixesContextPos(subMenuRef, subMenuListRef, 'submenu')
          this.menuPos = {
            left: `${menuPos.left}px`,
            top: `${menuPos.top}px`,
            zIndex
          }
        })
      }
    },
    showSubmenuHandler () {
      clearTimeout(this.outTimer)
      this.enterTimer = setTimeout(() => {
        this.showSubmenu = true
        this.initMenuPos()
      }, 300)
    },
    hideSubmenuHandler () {
      clearTimeout(this.enterTimer)
      this.outTimer = setTimeout(() => {
        this.showSubmenu = false
      }, 300)
    }
  }
}
</script>
<style lang="scss" scoped>
.beauti-contextmenu-submenu {
  position: relative;
  margin:0;
  display: flex;
  box-sizing: border-box;
  list-style: none;

  span {
    display: inline-block;

    &.icon {
      width: 25px;
    }

    &.content {
      flex:1;
    }

    &.more {
      position: relative;
      width: 15px;
      margin-left: 15px;

      img {
        position: absolute;
      }
    }
  }

  .beauti-contextmenu-sub-ul {
    position: absolute;
    padding:4px 0;
    margin:0;
    border-radius:5px;
    box-shadow: 0 5px 10px rgba(0,0,0,.15);
    background-color: #fff;
    opacity: .97;
  }
}
</style>
